import { FornecedorServiceProvider } from './../../providers/fornecedor-service/fornecedor-service';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { TabsPage } from './../tabs/tabs';
import { Utils } from './../../entity/Utils';
import { ItemServiceProvider } from './../../providers/item-service/item-service';
import { Item } from './../../entity/Item';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-item-edit',
  templateUrl: 'item-edit.html',
  providers: [ItemServiceProvider, FornecedorServiceProvider]
})
export class ItemEditPage {
  showFile: boolean;
  showImage1: boolean;
  itemUrl:string;
  model: Item;
  fornecedores: any;
  page: number;

  selectedFiles1: FileList;
  currentFileUpload1: File;
  progress1: { percentage: number } = { percentage: 0 };

  constructor(public navCtrl: NavController,
              public loadingCtrl: LoadingController,
              public toastCtrl: ToastController,
              public navParams: NavParams,
              private itemProvider: ItemServiceProvider,
              private fornecedorProvider: FornecedorServiceProvider) 
  {
    this.itemUrl = Utils.getUrlBackEnd() + "item/";
    if (this.navParams.data.item) {
      this.model = this.navParams.data.item;
      if (this.model.fornecedor)
        this.model.fornecedor_id = this.navParams.data.item.fornecedor.id;
      else 
        this.model.fornecedor_id = -1;
      if (this.model.imagem1 != null) 
        this.showImage1 = true;
      else
        this.showImage1 = false;
    } else {
      this.model = new Item();
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ItemEditPage');
    this.fornecedores = [];
    this.page = 1;
    this.getAllFornecedores();
  }

  getAllFornecedores() {    
    this.fornecedorProvider.listAll().subscribe(
      response => { 
        this.fornecedores = response;
        console.log('Fornecedores: ' + this.fornecedores);
      }
    );
  }

  save() {
    let fornecedor = this.model.fornecedor_id;
    this.saveItem().subscribe(
      (res: Item) => {
        this.model = res;
      },
        (error) => {
          this.toastCtrl.create({ message: 'Erro ao salvar item. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      },
      () => {
        if (fornecedor)
          this.updateFornecedor(this.model.id, fornecedor);
        this.uploadImages(this.model.id);

        this.toastCtrl.create({ message: 'Item salvo com sucesso. Id: ' + this.model.id, position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage)
      }
    );

  }

  private saveItem() {
    if (this.model.id) {
      return this.itemProvider.update(this.model);
    } else {
      return this.itemProvider.insert(this.model);
    }
  }

  updateFornecedor(id: number, fornecedor_id: number) {
    //this.itemProvider.updateFornecedor(id, fornecedor_id).subscribe();
    this.itemProvider.updateFornecedor(id, fornecedor_id).subscribe(res => console.log(res));
    console.log("updateFornecedor");
  }

  uploadImages(id: number) {
    this.progress1.percentage = 0;
 
    if (this.selectedFiles1 != undefined)
      this.currentFileUpload1 = this.selectedFiles1.item(0);    
    
    this.itemProvider.pushFilesToStorage(id, this.currentFileUpload1).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress1.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('Files are completely uploaded!');
      }
    });

    this.selectedFiles1 = undefined;
  }

  selectFile1(event) {
    this.selectedFiles1 = event.target.files;
  }

  showFiles(enable: boolean) {
    this.showFile = enable
  }
  
  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom'
    });
  
    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });
  
    toast.present();
  }

}


